.App {
  text-align: center;
  margin-top: 50px;
}

form {
  margin-bottom: 20px;
}

input {
  padding: 10px;
  margin-right: 10px;
}
div{
  display: flex;
  flex-direction: column;
  align-items: center;
}
button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
  max-width: 100px;
}

button:hover {
  background-color: #0056b3;
}

h3 {
  color: red;
}

img {
  max-width: 400px;
  height: auto;
  margin-bottom: 20px;
}
